import React from 'react';
import Header from './Header.jsx';
import {connect} from 'react-redux';
import {setMeAuth, setMeLoggedOut} from './../../redux/auth-reducer.js';

class HeaderComponentAPI extends React.Component {
    componentDidMount() {
      this.props.setMeAuth();
    }
    logOut = () => {
      this.props.setMeLoggedOut();
    }
    render = () => {
      return (
        <Header logOut={this.logOut} userData={this.props}/>
      )
    }
}

const mapStateToProps = (state) => {
    return (
      {
        isAuth: state.authMe.isAuth,
        login: state.authMe.login
      }
    )
}

const HeaderComponent = connect(mapStateToProps, {setMeAuth, setMeLoggedOut})(HeaderComponentAPI);

export default HeaderComponent;
