import {profileAPI} from './../api/api.js';

const ADD_POST = 'ADD-POST';
const SET_USER = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';

let initialData = {
  postData: [
    {id: 1, message: "Woof!", likeCount: 5},
    {id: 2, message: "Today am a good boy!", likeCount: 8},
    {id: 3, message: "Where is my tail?", likeCount: 9998}
  ],
  profile: null,
  status: ''
};

const profileReducer = (state = initialData, action) => {
  switch (action.type) {
    case ADD_POST:
      let newPost = {
          id: 4,
          message: action.newPost,
          likeCount: 0
      }
      return {
        ...state,
        postData: [...state.postData, newPost]
      }
    case SET_USER:
        return {
          ...state,
          profile: action.profile
        }
    case SET_USER_STATUS:
        return {
          ...state,
          status: action.status
        }
    default: return state;
  }
}

export default profileReducer;

export const onAddPost = (newPost) => {
  return {type: ADD_POST, newPost};
}
export const setUserProfile = (profile) => {
  return {type: SET_USER, profile}
}
export const setUserStatus = (status) => {
  return {type: SET_USER_STATUS, status}
}

export const getUserProfile = (id) => {
  return (dispatch) => {
    if (!id) {
      id = 1802;
    }
    profileAPI.getProfile(id).then(data => {
      dispatch(setUserProfile(data));
    })
  }
}
export const getUserStatus = (id) => {
  return (dispatch) => {
    profileAPI.getStatus(id).then(data => {
      dispatch(setUserStatus(data));
    })
  }
}

export const setProfileStatus = (status) => {
  return (dispatch) => {
    profileAPI.setStatus(status).then(data => {
      if (data.resultCode === 0) {
        dispatch(setUserStatus(status));
      }
    })
  }
}
