import React from 'react';
import styles from './../Profile.module.css';
import banner from './../../../assets/images/banner.jpg';
import Preloader from './../../Shared/Preloader.jsx';
import Avatar from './../../Shared/Avatar.jsx';
import ProfileStatusWithHooks from './ProfileStatus/ProfileStatus.jsx';

const Profile = (props) => {
  const renderSocialLinks = (contacts) => {
    return (
      Object.entries(contacts).map(([caption, url]) => {
          if (!url) {
            return '';
          }
          const newUrl = /^http/.test(url) ? url : 'https://' + url;
          return <span key="caption">
            {caption}: <a target="_new" href={newUrl}>Link</a>
          </span>
      })
    )
  }
  if (!props.userId) {
    return  <Preloader />
  }
  return (
    <div className={styles.profileWrapper}>
      <div>
        <img className={styles.banner} src={banner} alt="User banner"/>
      </div>
      <div className={styles.info}>
        <Avatar className={styles.profile__image} src={props.photos.large} name={props.fullName}/>
        <span className={styles.full__name}>{props.fullName}</span>
        <div className={styles.status}>
          <ProfileStatusWithHooks status={props.status} setProfileStatus={props.setProfileStatus}/>
        </div>
        <div>
          <span>{props.lookingForAJob ? 'Ищу работу.' : 'Уже работаю!'}</span>
          <span> {props.lookingForAJobDescription}</span>
        </div>
        <div className={styles.contacts}>
          {renderSocialLinks(props.contacts)}
        </div>

      </div>
    </div>
  )
}

export default Profile;
