import {authAPI} from './../api/api.js';
import {stopSubmit} from 'redux-form';

const AUTH_ME = 'FOLLOW';
const LOGGED_OUT = 'LOGGED_OUT';

let initialData = {
  id: null,
  email: null,
  login: null,
  isAuth: false
}

const userAuthMe = (state = initialData, action) => {
  switch(action.type) {
    case AUTH_ME:
      return {
        ...state,
        ...action.data,
        isAuth: true
      }
    case LOGGED_OUT:
      return {
        ...state,
        id: null,
        email: null,
        login: null,
        isAuth: false
      }
    default: return state;
  }
}

export default userAuthMe;

export const setUserAuthMe = (id, email, login) => {
    return {type: AUTH_ME, data: {id, email, login}}
}

const setUserLoggedOut = () => {
  return {type: LOGGED_OUT}
}

export const setMeAuth = () => {
  return (dispatch) => {
    return (
      authAPI.getAuthMe().then(data => {
        if (data.resultCode === 0) {
          let {email, id, login} = data.data;
          dispatch(setUserAuthMe(id, email, login));
        }
      })
    )
  }
}

export const setMeLoggedIn = (email, password, rememberMe = false) => {
  return (dispatch) => {
    authAPI.getLoggedMe(email, password, rememberMe).then(data => {
      if (data.resultCode === 0) {
        dispatch(setMeAuth());
      } else if (data.resultCode === 1) {
        dispatch(stopSubmit("Login", {_error: "Incorrect email or password."}));
      } else if (data.resultCode === 10) {
        dispatch(stopSubmit("Login", {_error: "You need to enter caption. We don't have caption yet. So ... try in an hour, I guess."}))
      }
    })
  }
}

export const setMeLoggedOut = () => {
  return (dispatch) => {
    authAPI.getLoggedOut().then(data => {
      if (data.resultCode === 0) {
        dispatch(setUserLoggedOut());
      }
    })
  }
}
