import React from 'react';
import styles from './Login.module.css';
import {Field} from 'redux-form';
import {required, maxLengthCreator, minLength8} from './../Shared/Validator.js';
import {Input} from './../Shared/Textarea/Textarea.jsx';

const maxLength20 = maxLengthCreator(20);

const LoginForm = (props) => {
    return (
      <form onSubmit={props.submit} className={styles.login__form}>
          <Field name="email" validate={[required, maxLength20]} component={Input} placeholder="Email" type="text"></Field>
          <Field name="password" validate={[required, minLength8]} component={Input} placeholder="Password" type="password"></Field>
          <span><Field name="rememberMe" component={Input} type="checkbox"></Field> Remember me.</span>
          <button type="submit">Submit</button>
          {props.error ? <span className={styles.commonError}>{props.error}</span> : ""}
        </form>
    )
}

export default LoginForm;
