import React, {useState, useEffect} from 'react';
import styles from './../../Profile.module.css';

const ProfileStatusWithHooks = (props) => {

  let [editMode, setEditMode] = useState(false);
  let [status2, setStatus] = useState(props.status);

  useEffect( () => {
    setStatus(props.status);
  }, [])

  const activateEditMode = () => {
    setEditMode(true);
  }

  const deActivateEditMode = () => {
    setEditMode(false);
    props.setProfileStatus(status2);
  }

  const updateStatusText = (e) => {
    setStatus(e.currentTarget.value);
  }

  if (!editMode) {
    return <span onDoubleClick={() => {activateEditMode()}}>{status2 || '----'}</span>
  } else if (editMode) {
    return <input autoFocus={true} onBlur={() => {deActivateEditMode()}} value={status2} onChange={updateStatusText} ></input>
  }
}

export default ProfileStatusWithHooks;
