import React from 'react';
import {reduxForm} from 'redux-form';
import LoginForm from './LoginForm.jsx';
import {setMeLoggedIn} from './../../redux/auth-reducer.js'
import {connect} from 'react-redux';
import {Redirect} from 'react-router';

let Login = (props) => {
  const { handleSubmit } = props;
  return  (
    <LoginForm error={props.error} submit={handleSubmit}/>
  )
}

Login = reduxForm({
  form: 'Login'
})(Login)

class LoginPage extends React.Component {
  submit = (values) => {
    this.props.setMeLoggedIn(values.email, values.password, values.rememberMe);
  }
  render() {
    return (this.props.isAuth? <Redirect to="/profile"/> : <Login onSubmit={this.submit} />)
  }
}

const mapStateToProps = (state) => {
    return {
      isAuth: state.authMe.isAuth
    }
}

export default connect(mapStateToProps, {setMeLoggedIn})(LoginPage);
