import {authAPI} from './../api/api.js';
import {setMeAuth} from './auth-reducer.js';

const INITILIZE_APP = 'INITILIZE_APP';

let initialData = {
  initializied: false
}

const appInitialize = (state = initialData, action) => {
  switch(action.type) {
    case INITILIZE_APP:
      return {
        ...state,
        initializied: true
      }
    default: return state;
  }
}

const setAppInitialize = () => {
  return {type: INITILIZE_APP}
}

export default appInitialize;

export const setAppInitializeThunkCreator = () => {
  return (dispatch) => {
    dispatch(setMeAuth()).then(() => {
      dispatch(setAppInitialize());
    });
  }
}
