import React from 'react';
import styles from './Profile.module.css';
import PostsContainer from './Posts/PostsContainer.jsx';
import Profile from './Profile/Profile.jsx';
import {connect} from 'react-redux';
import {getUserProfile, getUserStatus, setProfileStatus} from './../../redux/profile-reducer.js'
import {withRouter} from 'react-router-dom';
import withAuthRedirect from './../../hoc/withAuthRedirect.js';
import {compose} from 'redux';

class ProfileAPI extends React.Component {
    componentDidMount() {
      let userID = this.props.match.params.userId;
      if (!userID) {
        if (this.props.isAuth) {
          userID = this.props.loggedUser;
        }
        else userID = '';
      }
      this.props.getUserProfile(userID);
      this.props.getUserStatus(userID);
    }

    render = () => {
      return <div className={styles.content}>
            <Profile {...this.props.profileData.profile}
              status={this.props.profileData.status}
              setProfileStatus={this.props.setProfileStatus}/>
            <PostsContainer
              store={this.props.store}
              dispatch={this.props.dispatch}
            />
        </div>
    }
}

let mapStateToProps = (state) => {
    return {
        profileData: state.profileData,
        isAuth: state.authMe.isAuth,
        loggedUser: state.authMe.id
    }
}

export default compose(
  withRouter,
  connect(mapStateToProps, {getUserProfile, getUserStatus, setProfileStatus}),
  withAuthRedirect
)(ProfileAPI);;
