import * as axios from 'axios';


const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
      'API-KEY': '932c0bbd-5279-433b-9a31-417bf30b4feb'
    }
});

export const usersAPI = {
  getUsers(currentPage = 1, pageCount = 10) {
    return instance.get(`users?page=${currentPage}&count=${pageCount}`).then(response => {
        return response.data;
    })
  },

  getProfile(userID) {
    console.log("this is old method, use profileAPI.getProfile");
    profileAPI.getProfile(userID);
  },

  followUser(id) {
    return instance.post('follow/' + id).then(response => {
        return response.data;
    })
  },

  unfollowUser(id) {
    return instance.delete('follow/' + id).then(response => {
        return response.data;
    })
  }
}

export const authAPI = {
  getAuthMe() {
    return instance.get('auth/me').then(response => {
        return response.data;
    })
  },
  getLoggedMe(email, password, rememberMe) {
    return instance.post('auth/login', {email: email, password: password, remmberMe: rememberMe}).then(response => {
        return response.data;
    })
  },
  getLoggedOut() {
    return instance.delete('auth/login').then(response => {
        return response.data;
    })
  }
}

export const profileAPI = {
  getProfile(userID) {
    return instance.get(`profile/${userID}`).then(response => {
        return response.data;
    })
  },
  getStatus(userID) {
    return instance.get(`profile/status/${userID}`).then(response => {
        return response.data;
    })
  },
  setStatus(status) {
    return instance.put(`profile/status`, {status: status}).then(response => {
        return response.data;
    })
  }
}
