import React from 'react';
import styles from './Posts.module.css';
import Post from './Post/Post.jsx';
import {Field, reduxForm} from 'redux-form';
import {maxLengthCreator, required} from './../../Shared/Validator.js';
import {Textarea} from './../../Shared/Textarea/Textarea.jsx';

const Posts = (props) => {
  let postElemets = props.postData.map( post => <Post key={post.id} message={post.message} likeCount={post.likeCount}/>);
  let newPostElement = React.createRef();
  let addPost = (values) => {
    props.onAddPost(values.postText);
  }
  return (
    <div className={styles.posts}>
      My posts
      <div className={styles.item}>
        New post
        <PostFormRedux onSubmit={addPost}/>
      </div>
      <div className={styles.reverseOrder}>
        {postElemets}
      </div>
    </div>
  )
}

const maxLength50 = maxLengthCreator(50);

const PostForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field name="postText" component={Textarea} validate={[required, maxLength50]} placeholder="Add new post here."></Field>
      <div className={styles.button__group}>
        <button type="submit">Add</button>
        <button>Edit</button>
        <button>Delete</button>
      </div>
    </form>
  )
}

const PostFormRedux = reduxForm({form: 'manageUserPost'})(PostForm)

export default Posts;
