import React from 'react';
import './App.css';
import HeaderComponent from './components/Header/HeaderComponent.jsx';
import Navbar from './components/Navbar/Navbar.jsx';
import WithUrlDataContainerComponent from './components/Profile/ProfileContainer.jsx';
import News from './components/News/News.jsx';
import Music from './components/Music/Music.jsx';
import Settings from './components/Settings/Settings.jsx';
import LoginPage from './components/Login/LoginPage.jsx';
import {setAppInitializeThunkCreator} from './redux/app-reducer.js';
import Preloader from './components/Shared/Preloader.jsx';
import {compose} from 'redux';
import store from './redux/redux-store.js';
import {withRouter, HashRouter, Route} from "react-router-dom";
import {Provider, connect} from 'react-redux';
import withSuspense from './hoc/withSuspense.js';

// lazy loading:

const DialogsContainer = React.lazy(() => import ('./components/Dialogs/DialogsContainer.jsx'));
const UsersContainer = React.lazy(() => import ('./components/Users/UsersContainer.jsx'));



class App extends React.Component {
  componentDidMount() {
    this.props.setAppInitializeThunkCreator();
  }
  render() {
    if (!this.props.isAppInitialized) {
      return <Preloader />
    }
    return (
      <div className='app-wrapper'>
        <HeaderComponent />
        <Navbar friends={this.props.state.friendsData}/>
        <Route path='/login' component={LoginPage}/>
        <div className='app-wrapper-content'>
          <Route path='/dialogs' render={withSuspense(DialogsContainer)}/>
          <Route path='/profile/:userId?' render={ () =>
            <WithUrlDataContainerComponent />}/>
          <Route path='/news' component={News}/>
          <Route path='/music' component={Music}/>
          <Route path='/settings' component={Settings}/>
          <Route path='/users' render={withSuspense(UsersContainer)}/>
        </div>
      </ div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isAppInitialized: state.app.initializied
  }
}

let AppContainer = compose(
  withRouter,
  connect(mapStateToProps, {setAppInitializeThunkCreator})
)(App);

const SamuraiJsApp = (props) => {
  return <HashRouter>
    <Provider store={store}>
      <AppContainer state={store.getState()}/>
    </Provider>
  </HashRouter>
}

export default SamuraiJsApp;
